<script>
import SpecialTitle from './SpecialTitle'

export default {
    props: {
        productIdisCode: Array,
        productVO: Object
    },
    computed: {
        infoList() {
            const res =
                this.productIdisCode.length &&
                this.productIdisCode.map(item => {
                    return {
                        name: this.productVO.productName,
                        value: item
                    }
                })
            return res
        }
    },
    components: {
        SpecialTitle
    }
}
</script>

