<template>
    <div :class="`home-msg ${userToken ? '' : 'home-msg-pb'}`">
        <div class="home-msg-cont">
            <div v-for="item in template.items" :key="item.id">
                <template-head v-if="item.type === 'templateHead'" :item="item"></template-head>
                <template-carousel v-if="item.type === 'carousel'" :item="item"></template-carousel>
                <div v-if="item.type === 'carousel' && userInfo.avatar && isWeixinHeadUsed()" class="user-avatar">
                    <img :src="userInfo.avatar" :lazy="true" class="user-avatar-img" />
                    <div class="user-avatar-bg"></div>
                </div>
                <product-desc v-if="item.type === 'productIntroduction'" :product="item.ad.product"></product-desc>
                <related-product v-if="item.type === 'relatedProduct'" :list="item.ad.list"></related-product>
                <validate-search v-if="item.type === 'antiCounterfeit'" :productName="code.productName" :tenant="tenant" :idis-code="code.idisCode" @getLocation="getLocation" :item="item"></validate-search>
                <template-text v-if="item.type === 'text'" :item="item"></template-text>
                <template-video v-if="item.type === 'video'" :item="item"></template-video>
                <!-- <template-pictures v-if="item.type === 'pictures'" :item="item"></template-pictures> -->
                <ProductInclude v-if="item.type === 'productInfo' && Array.isArray(code.relatedCodes) && code.relatedCodes.length > 0" :list="code.relatedCodes"></ProductInclude>
                <product-info v-if="item.type === 'productInfo'" :item="item" :code="item.ad.code" :code-attrs="item.ad.codeAttrs" :batch="item.ad.batch" :sourceFlag="sourceFlag" :isView="true" @convertRouter="wxSource(item.ad.code.idisCode)"></product-info>

                <product-detail v-if="item.type === 'productInfo' && productIdisCode.length" :productIdisCode="productIdisCode" :productVO="productVO"></product-detail>
                <template-stylebox v-if="item.type === 'stylebox'" :item="item" :code="code"></template-stylebox>
                <code-information v-if="item.type === 'codeInformation'" :item="item" :code="item.ad.code">
                </code-information>
                <product-barcode v-if="item.type === 'productBarcode'" :item="item" :code="code"></product-barcode>
                <product-images v-if="item.type === 'productImages' && item.ad.product.images.length > 0" :item="item" :images="item.ad.product.images"></product-images>

                <product-price v-if="item.type === 'productPrice'" :item="item" :code="item.ad.code"></product-price>
                <product-quality-guarantee-period v-if="item.type === 'productQualityGuaranteePeriod'" :item="item" :code="item.ad.code" :batch="item.ad.batch"></product-quality-guarantee-period>
                <three-img v-if="item.type === 'threeImg'" :item="item"></three-img>
            </div>
            <div class="home-msg-logout" v-if="userToken && userToken !== 'userToken'" @click="handleLogout">退出登录</div>
            <div v-if="template.config.showBottom" style="height: 60px"></div>

        </div>
        <div style="position: fixed; bottom: 0px; width: 100%; z-index: 10000">
            <base-music v-if="musicComp && musicComp.visible" :item="{
                    value: {
                        music: musicComp.value.music,
                        bgcolor: musicComp.value.bgcolor
                    }
                }" style="float: right; height: 40px; width: 40px; margin-bottom: 10px; margin-right: 10px">
            </base-music>
            <bottom-navigator v-if="template.config.showBottom" :code="code" active="home"></bottom-navigator>
        </div>

        <div class="home-msg-bug"></div>

        <div class="home-msg-login" v-if="!userToken && userToken !== 'userToken'">
            <div>登录获得更多内容</div>
            <button @click="handleLogin">登录</button>
        </div>
    </div>
</template>

<script>
const defaultIcon = require('@/assets/images/logo.png')
import { getJsonResult } from '@/utils/server-wrapper'
import { postJson } from '@/utils/server'
import { getJsTicket } from '@/utils/weixin'
import CONFIG from '@/components/product-template/config'

import RelatedProduct from '@/components/product-template/RelatedProduct'
import ProductDesc from '@/components/product-template/ProductDesc'
import ProductInfo from '@/components/product-template/ProductInfo'
import ProductDetail from '@/components/product-template/ProductDetail'
import ProductInclude from '@/components/product-template/productInclude'
import ValidateSearch from '@/components/product-template/ValidateSearch'
import TemplatePictures from '@/components/product-template/TemplatePictures'
import TemplateVideo from '@/components/product-template/TemplateVideo'
import TemplateStylebox from '@/components/product-template/TemplateStylebox'
import TemplateText from '@/components/product-template/TemplateText'
import TemplateHead from '@/components/product-template/TemplateHead'
import TemplateCarousel from '@/components/product-template/TemplateCarousel'
import CodeInformation from '@/components/product-template/CodeInformation'
import ProductImages from '@/components/product-template/ProductImages'
import ProductBarcode from '@/components/product-template/ProductBarcode'
import ProductPrice from '@/components/product-template/ProductPrice'
import ProductQualityGuaranteePeriod from '@/components/product-template/ProductQualityGuaranteePeriod'
import { createGetLocation } from '@/utils/platform'
import BaseMusic from '@/components/product-template/BaseMusic'
import BottomNavigator from '@/components/product-template/BottomNavigator'
import ThreeImg from '@/components/product-template/ThreeImg'

export default {
    data() {
        return {
            defaultIcon,
            userInfo: {},
            idisCode: '',
            tenant: { id: 0, name: '' },
            template: {
                items: [],
                config: {
                    title: '',
                    carouselList: []
                }
            },
            productIdisCode: [],
            productVO: '',
            code: {},
            locationMethod: null,
            token: null,
            userToken: 'userToken',
            sourceFlag: null
        }
    },
    computed: {
        musicComp() {
            const list = this.template.items.filter(i => i.type === 'backMusic')
            return list.length == 0 ? null : list[0]
        }
    },
    components: {
        RelatedProduct,
        ProductDesc,
        ProductInfo,
        ProductInclude,
        ValidateSearch,
        TemplatePictures,
        TemplateVideo,
        TemplateStylebox,
        TemplateHead,
        TemplateText,
        TemplateCarousel,
        CodeInformation,
        ProductImages,
        ProductPrice,
        ProductQualityGuaranteePeriod,
        ProductBarcode,
        BaseMusic,
        BottomNavigator,
        ThreeImg,
        ProductDetail
    },
    mounted() {
        if (!Array.isArray(this.code.relatedCodes) || !this.code.relatedCodes.length) {
            if (this.$route.query.token) {
                this.userToken = this.$route.query.token
                localStorage.setItem('token', this.$route.query.token)
            } else {
                this.userToken = localStorage.getItem('token') || ''
            }
        }

    },
    methods: {
        // 跳转到溯源解析
        wxSource(idisCode) {
            // window.location.href = `http://192.168.110.236:8002/weixin/#/trace/trace-analysis?idisCode=${idisCode}`
            window.location.href = `${window.location.origin}/weixin.html#/trace/trace-analysis?idisCode=${idisCode}`
            //this.$router.push({ path: '/frontend/weixin/trace/trace-analysis', query: { idisCode } })
        },
        getLocation(callback) {
            let _this = this
            this.locationMethod = this.locationMethod || createGetLocation()
            this.locationMethod(function (geoParam) {
                geoParam.codeId = _this.code.id
                geoParam.idisCode = _this.code.idisCode
                geoParam.templateId = _this.template.id
                geoParam.token = _this.token
                callback(geoParam)
            })
        },
        isWeixinHeadUsed() {
            return false
        },

        handleLogin() {
            // TODO
            window.location.href = `${window.location.origin}/xicha/index.html#/?code=${this.idisCode}`
        },

        handleLogout() {
            localStorage.removeItem('token')
            location.reload()
        }
    },
    async beforeRouteEnter(to, from, next) {
        const idisCode = to.query.code
        const token = to.query.token
        const info = await getJsonResult(`/wx/codeInformation?codeId=${encodeURIComponent(idisCode)}`)
        if (!info) {
            next({
                path: '/error',
                query: {
                    status: 404,
                    msg: '找不到码信息'
                }
            })
            return
        }
        // 获取产品明细
        //const detail = await getJsonResult(`/packing/getPackageInfo?idisCode=${encodeURIComponent(idisCode)}`)

        // const productIdisCode = detail.idisCode
        // const productVO = detail.productVO
        const code = info.code
        if (Array.isArray(code.relatedCodes) && code.relatedCodes.length) {
            for (let i = 0; i < code.relatedCodes.length; i++) {
                const item = code.relatedCodes[i]
                if (item.imgIcon) {
                    const imageData = JSON.parse(item.imgIcon)
                    if (Array.isArray(imageData.images) && imageData.images.length) {
                        item.icon = imageData.images[0]
                    }
                }
                item.attrData = {}
                if (Array.isArray(item.businessObjectAttrList) && item.businessObjectAttrList.length) {
                    for (let j = 0; j < item.businessObjectAttrList.length; j++) {
                        const attrItem = item.businessObjectAttrList[j]
                        switch (attrItem.attrNameCn) {
                            case '归属地':
                                item.attrData.attribution = attrItem.attrValue
                                break
                            case '硒含量':
                                item.attrData.Se = attrItem.attrValue
                                break
                            case '经纬度':
                                item.attrData.tude = attrItem.attrValue
                                break
                        }
                    }
                }
            }
        }
        console.log(code.relatedCodes)
        const template = info.template
        const tenant = info.tenant
        const codeAttr = info.codeAttr
        const productId = code.boId

        // 获取相关信息
        let product, relatedProduct
        for (let item of template.items) {
            if (item.type === 'productIntroduction' || item.type === 'productImages') {
                // 获取产品信息
                if (product === undefined) {
                    product = await getJsonResult(`/wx/productInfo?productId=${productId}&codeId=0`)
                }
            } else if (item.type === 'relatedProduct') {
                // 获取关联产品
                if (relatedProduct === undefined) {
                    // TODO 和后端沟通 暂时隐藏
                    // relatedProduct = await getJsonResult(`/wx/item/relatedProduct?templateId=${template.id}`)
                    // relatedProduct = relatedProduct.map(i => {
                    //     i.productImage = i.productImage || defaultIcon
                    //     return i
                    // })
                }
            }
        }

        // 筛选模板中需要隐藏的内容
        template.items = template.items
            .filter(i => i.sort > 0 && i.visible)
            .filter(item => {
                if (item.type === 'productPrice') {
                    return !!code.price
                } else if (item.type === 'productQualityGuaranteePeriod') {
                    return info.batch.productionDate && info.batch.beyondShelfLifeDate
                } else if (item.type === 'antiCounterfeit') {
                    return info.hasCheckCode
                } else if (item.type === 'productBarcode') {
                    return !!code.barCode
                } else if (item.type === 'productIntroduction') {
                    return !!product.remark
                } else if (item.type === 'relatedProduct') {
                    return relatedProduct.length > 0
                } else if (item.type === 'productImage') {
                    return product.images && product.images.length
                }
                return true
            })
            .sort((a, b) => a.sort - b.sort)

        // 设置默认值
        for (const item of template.items) {
            item.value = item.value ? (typeof item.value === 'string' ? JSON.parse(item.value) : {}) : {}
            if (CONFIG[item.type]) {
                const defaultValue = CONFIG[item.type].defaultValue
                if (!(defaultValue instanceof Array)) {
                    for (let key of Object.keys(defaultValue)) {
                        if (item.value[key] === undefined) {
                            item.value[key] = defaultValue[key]
                        }
                    }
                }
            }
        }

        let applyWeixin = false
        // 设置模板的内容
        for (const item of template.items) {
            let ad = {}
            item.ad = ad
            if (item.type === 'templateHead') {
                if (!item.value.title) {
                    item.value.title = code.productName
                }
            } else if (item.type === 'productIntroduction' || item.type === 'productImages') {
                ad.product = product
            } else if (item.type === 'relatedProduct') {
                ad.list = relatedProduct
            } else if (
                item.type === 'productInfo' ||
                item.type === 'codeInformation' ||
                item.type === 'productBarcode' ||
                item.type === 'productPrice' ||
                item.type === 'productQualityGuaranteePeriod'
            ) {
                ad.code = code
                ad.batch = info.batch
                ad.codeAttrs = codeAttr
            } else if (item.type === 'pictures') {
                if (item.value.link != null) {
                    item.value.link = item.value.link.replace(/\{productName\}/g, encodeURIComponent(code.productName))
                    item.value.link = item.value.link.replace(/\{batchNo\}/g, encodeURIComponent(code.batchNo))
                    item.value.link = item.value.link.replace(/\{code\}/g, encodeURIComponent(code.idisCode))
                }
            } else if (item.type === 'carousel') {
                if (item.value.url != null) {
                    item.value.url = item.value.url.replace(/\{productName\}/g, encodeURIComponent(code.productName))
                    item.value.url = item.value.url.replace(/\{batchNo\}/g, encodeURIComponent(code.batchNo))
                    item.value.url = item.value.url.replace(/\{code\}/g, encodeURIComponent(code.idisCode))
                }
            } else if (item.type === 'stylebox') {
                for (const row of item.value.rows) {
                    for (const cell of row) {
                        if (cell.linkType === 'suretyService') {
                            cell.href = `/wx/surety-service?productId=${productId}&tenantId=${tenant.id}`
                        } else if (cell.linkType === 'warranty') {
                            cell.href = `/wx/warranty?idisCode=${encodeURIComponent(idisCode)}`
                        } else if (cell.linkType === 'repair') {
                            cell.href = `/frontend/weixin/repair?idiscode=${encodeURIComponent(idisCode)}`
                        } else if (cell.linkType === 'companyIntroduction') {
                            cell.href = `/wx/tenant?tenantId=${tenant.id}`
                        } else if (cell.linkType === 'instruction') {
                            cell.href = `/wx/instruction?productId=${productId}`
                        } else if (cell.linkType === 'contactUs') {
                            cell.href = `/frontend/weixin/contact-us?tenantId=${tenant.id}`
                        } else if (cell.linkType === 'industrialMall') {
                            cell.href = `/wx/mall/mallHome?tenantId=${tenant.id}`
                        } else if (cell.linkType === 'officialQuery') {
                            cell.href = `https://dms.citln.cn/whois/ui/searchResult?identity=${encodeURIComponent(
                                idisCode
                            )}`
                        } else if (cell.linkType === 'maintenanceHistory') {
                            cell.href =
                                '/frontend/weixin/maintenance/code-history?idisCode=' + encodeURIComponent(idisCode)
                        } else if (cell.linkType === 'weixinWeapp') {
                            applyWeixin = true
                            cell.weixinWeappPath = cell.weixinWeappPath.replace(
                                /\{productName\}/g,
                                encodeURIComponent(code.productName)
                            )
                            cell.weixinWeappPath = cell.weixinWeappPath.replace(
                                /\{batchNo\}/g,
                                encodeURIComponent(code.batchNo)
                            )
                            cell.weixinWeappPath = cell.weixinWeappPath.replace(
                                /\{code\}/g,
                                encodeURIComponent(idisCode)
                            )
                        } else if (cell.href != null) {
                            cell.href = cell.href.replace(/\{productName\}/g, encodeURIComponent(code.productName))
                            cell.href = cell.href.replace(/\{batchNo\}/g, encodeURIComponent(code.batchNo))
                            cell.href = cell.href.replace(/\{code\}/g, encodeURIComponent(code.idisCode))
                        }
                    }
                }
            }
        }
        template.config = JSON.parse(template.config || '{}')

        //const userInfo = (await getJsonResult('/wx/consumer-info')) || {}
        next(vm => {
            vm.$data.idisCode = idisCode
            //vm.$data.productIdisCode = productIdisCode
            //vm.$data.productVO = productVO
            vm.$data.template = template
            vm.$data.code = code
            //vm.$data.userInfo = userInfo
            vm.$data.token = token
            vm.$data.tenant = tenant
            vm.sourceFlag = info.sourceFlag
            // vm.getLocation(function(loc) {
            //     postJson('/wx/save-query-location', loc)
            // })
            document.title = code.boName

            if (applyWeixin) {
                getJsTicket({
                    jsApiList: ['getLocation', 'updateAppMessageShareData', 'updateTimelineShareData'],
                    openTagList: ['wx-open-launch-weapp', 'wx-open-launch-app']
                })
            }
        })
    },
    watch: {
        // 监听路由是否变化
        '$route'(to, from) {
            console.log(to)
            if (to.query.code != from.query.code) {
                history.go(0)
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.home-msg {
    flex-shrink: 0;
    background-size: cover;
    &-cont {
        background-color: #f4f5f5;
    }
}

.home-msg-pb {
    padding-bottom: 63px;
}

.home-msg-logout {
    margin: 20px 16px;
    height: 48px;
    font-size: 15px;
    text-align: center;
    line-height: 46px;
    border-radius: 2px;
    border: 1px solid #e3e3e3;
    background: #eeeeee;
}

.home-msg-bug {
    z-index: 11;
    position: fixed;
    bottom: 120px;
    right: 16px;
    width: 77px;
    height: 77px;
    background-size: 100% 100%;
    background-image: url(~@/assets/images/buy-btn.png);
}

.home-msg-login {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    z-index: 11;
    position: fixed;
    bottom: 66px;
    left: 0;
    right: 0;
    height: 51px;
    padding: 0 16px 0 15px;
    background-color: rgba(0, 0, 0, 0.7);
}

.home-msg-login div {
    font-size: 14px;
    color: #fff;
}

.home-msg-login button {
    width: 75px;
    height: 28px;
    padding: 0;
    border: none;
    outline: none;
    border-radius: 2px;
    font-size: 14px;
    text-align: center;
    background-color: #185ef0;
    color: #fff;
}

.user-avatar {
    position: relative;
    top: -10px;
    z-index: 1000;
    width: 100%;
    height: 60px;

    &-img {
        position: relative;
        width: 60px;
        height: 60px;
        border-radius: 50%;
        background-color: white;
        z-index: 1002;
    }

    &-bg {
        position: absolute;
        top: 10px;
        z-index: 1001;
        width: 100%;
        height: 50px;
        background-color: white;
    }
}

.mh-image {
    width: 25px;
    height: 25px;
    border-radius: 25px;
    border: 1px solid #e9e9e9;
    margin: 0 10px 0 18px;
}

.mh-text {
    font-size: 16px;
    color: #fff;
    letter-spacing: 2px;
}

.mh-icon {
    font-size: 20px;
    color: #fff;
    margin-right: 20px;
}

.car-image {
    width: 100%;
    height: 100%;
}

.mc-wrap {
    background-color: #ffffff;
    padding: 15px;
    margin-top: 5px;
    margin-bottom: 5px;

    &-title {
        color: #1c2023;
        font-size: 16px;
        text-align: left;
    }
}

.mc-link-circle {
    background-color: #e0e0e0;
    width: 20px;
    height: 20px;
    border-radius: 20px;
}
</style>
