/* eslint-disable */

const ua = navigator.userAgent
let isIOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
let geoParam = undefined

if (isIOS) {
    window.entryUrl = location.href.split('#')[0]
}

function createGetLocation() {
    let waitList = []

    function getGaodeLocation() {
        AMap.plugin('AMap.Geolocation', function () {
            var geoLocationApi = new AMap.Geolocation({
                enableHighAccuracy: true,
                timeout: 10000
            })
            geoLocationApi.getCurrentPosition(function (status, result) {
                console.log('位置信息', result);
                if (status === 'complete' && result.addressComponent) {
                    let comp = result.addressComponent
                    let adCode = comp.adcode
                    setupGeoParam({
                        longitude: result.position.lng,
                        latitude: result.position.lat,
                        accessAddress: result.formattedAddress,
                        provinceName: comp.province,
                        cityName: comp.city || comp.province,
                        adName: comp.district,
                        adCode: adCode,
                        cityCode: adCode.substring(0, 4) + '00',
                        provinceCode: adCode.substring(0, 2) + '0000'
                    })
                } else {
                    console.log('getGaodeLocation error', result)
                    setupGeoParam({
                        error: result
                    })
                }
            })
        })
    }

    function setupGeoParam(loc) {
        if (!loc.error) {
            geoParam = loc
        }
        for (var i = 0; i < waitList.length; i++) {
            waitList[i](Object.assign({}, loc))
        }
        waitList = []
    }

    return function (onComplete) {
        if (geoParam) {
            onComplete(geoParam)
            return
        }
        waitList.push(onComplete)
        if (waitList.length > 1) {
            return
        }

        // 使用高德地图定位
        getGaodeLocation()
    }
}

export { createGetLocation }
