<template>
    <div class="mc-wrap f14" style="padding-top: 10px; padding-bottom: 10px">
        <div v-if="stage === 'query'" class="validate-edit">
            <div class="public-title">
                <div>验证查询</div>
                <img src="@/assets/images/genuine.png" alt="" />
            </div>
            <el-form ref="form" class="mt12 flex-all-center">
                <el-form-item label-width="0" style="width: 90%" :error="errorMsg">
                    <el-input
                        class="vc-input flex-1"
                        placeholder="请输入防伪码查询"
                        @input="errorMsg = ''"
                        v-model="validateCode"
                    >
                        <template slot="append">
                            <el-button
                                class="vs-btn f14 c-white"
                                :style="{
                                    'background-color': item.value.buttonColor,
                                    color: item.value.buttonTextColor
                                }"
                                @click="onSearch"
                                >{{ item.value.buttonText }}</el-button
                            >
                        </template>
                    </el-input>
                </el-form-item>
            </el-form>
        </div>
        <div v-else-if="stage == 'success'" class="query-result">
            <div v-show="playVoice" style="position: absolute; top: 10px; right: 10px;" @click="stopVoice">
                <img style="height: 20px" :src="require('@/assets/images/audio-on.gif')" />
            </div>
            <p style="font-weight: bold; color: #38bf4d; margin-top: 20px">
                {{ item.value.validTitle }}
            </p>
            <p style="font-weight: bold; font-size: 18px; margin: 20px 0 20px 0">{{ productName }}</p>
            <p class="result-text">{{ successText }}</p>
            <div style="width: 100%; text-align: right; position: relative; top: -80px; right: 15px; height: 20px">
                <img :src="require('@/assets/images/validate-stamp-success.png')" style="width: 100px" />
            </div>
        </div>
        <div v-else class="query-result">
            <div>
                <p style="font-weight: bold; color: #fb0000; margin-top: 20px">
                    {{ item.value.invalidTitle }}
                </p>
                <p style="font-weight: bold; font-size: 18px; margin: 20px 0 20px 0">{{ productName }}</p>
                <p class="result-text">{{ failureText }}</p>
            </div>
            <!-- <router-link v-if="item.value.queryHistory" :to="`/check-code-history?idisCode=${encodeURIComponent(idisCode)}`" style="margin-top: 20px; text-decoration: none; color: gray">
                点击查看最近10条记录
            </router-link> -->
        </div>
        <audio id="Jaudio" ref="audio"></audio>
    </div>
</template>

<script>
import { getJsonResult, postJsonReturn } from '@/utils/server-wrapper'
import BaiduTTS from '@/utils/baidu_tts_cors'
const successTemplate = '您所查询的{product}是正品，谢谢您的查询。'
const failureTemplate =
    '您所查询的产品是{product}，该产品已被查询 {count} 次。如果您的验证码多次查询非个人行为，请确认您的产品是否为官方渠道购买！'

export default {
    props: {
        item: Object,
        idisCode: String,
        productName: String,
        tenant: Object
    },
    data() {
        return {
            stage: 'query',
            errorMsg: '',
            validateCode: '947918',
            successText: '',
            failureText: '',
            voiceText: '',
            playVoice: false
        }
    },
    methods: {
        async onSearch() {
            this.audioAutoPlay('Jaudio')
            if (!this.validateCode) {
                this.errorMsg = '请输入查验码!'
                return
            }
            const location = await this.getLocation()
            const queryResult = await getJsonResult(
                `/wx/anti-counterfeit-validate?checkCode=${this.validateCode}&idisCode=${this.idisCode}`
            )
            console.log(this.item)
            console.log('queryresult is', queryResult)
            // if (!queryResult.success) {
            //     this.errorMsg = '验证码错误!'
            // } else if (queryResult.times === 1) {
            //     this.showSuccess(queryResult)
            // } else {
            //     this.showFailure(queryResult)
            // }

            if (queryResult.success) {
                this.showSuccess(queryResult)
            } else {
                this.showFailure(queryResult)
            }
        },
        async audioAutoPlay(id) {
            var audio = document.getElementById(id),
                play = function() {
                    audio.play()
                    document.removeEventListener('touchstart', play, false)
                }
            audio.play()
            document.addEventListener(
                'WeixinJSBridgeReady',
                function() {
                    play()
                },
                false
            )
            document.addEventListener('touchstart', play, false)
        },
        getLocation() {
            var _this = this
            return new Promise(function(resolve, reject) {
                // _this.$emit('getLocation', async function (location) {
                //     if (location.error) {
                //         _this.errorMsg = '获取位置信息失败, 请确认允许获取位置信息'
                //         reject(location)
                //     } else {
                //         resolve(location)
                //     }
                // })
                resolve({})
            })
        },
        async showSuccess(queryResult) {
            //let url = `/wx/securityTemplate?id=${this.item.value.successTemplate || ''}&scenario=success`
            //const template = this.item//await getJsonResult(url)
            // const templateText = this.item.value.validTitlevalidTitle//template ? template.text : successTemplate
            // this.successText = this.replaceText(templateText, queryResult)
            // if (this.item.value.validVoice) {
            //     this.doVoice(this.successText)
            // }
            this.stage = 'success'
        },
        async showFailure(queryResult) {
            // let url = `/wx/securityTemplate?id=${this.item.value.failureTemplate || ''}&scenario=failure`
            // const template = await getJsonResult(url)
            // const templateText = template ? template.text : failureTemplate
            // this.failureText = this.replaceText(templateText, queryResult)
            this.stage = 'failure'
        },
        replaceText(text, queryResult) {
            //text = text.replace(/\{count\}/g, queryResult.times)
            text = text.replace(/\{product\}/g, this.productName)
            //text = text.replace(/\{firstQueryTime\}/g, queryResult.firstQueryTime)
            //text = text.replace(/\{lastQueryTime\}/g, queryResult.lastQueryTime)
            text = text.replace(/\{company\}/g, this.tenant.name)
            text = text.replace(/\{code\}/g, this.idisCode)
            text = text.replace(/\{validateCode\}/g, this.validateCode)
            return text
        },
        async doVoice(text) {
            if (this.voiceText === text) {
                setTimeout(() => this.this.audioAutoPlay('Jaudio'), 3000)
            } else {
                var _this = this
                const tok = await getJsonResult('/wx/getBaiDuToken')
                BaiduTTS(
                    {
                        tex: text,
                        tok,
                        spd: 5,
                        pit: 5,
                        vol: 15,
                        per: 1 // 度小宇=1，度小美=0，度逍遥（基础）=3，度丫丫=4
                    },
                    {
                        audio: this.$refs.audio,
                        timeout: 5000,
                        hidden: true,
                        onSuccess: function() {
                            _this.playVoice = true
                            _this.voiceText = text
                            _this.$refs.audio.play()
                        }
                    }
                )
            }
        },
        stopVoice() {
            this.playVoice = false
            this.$refs.audio.pause()
            this.$refs.audio.currentTime = 0
        }
    },
    mounted() {
        this.validateCode = this.$route.query.validateCode || this.$route.query.vc
        if (this.validateCode) {
            this.onSearch()
        }
        var _this = this
        this.$refs.audio.addEventListener('ended', function() {
            _this.playVoice = false
        })
    }
}
</script>

