import { getJsonResult } from './server-wrapper'

const weixin = /MicroMessenger/i.test(navigator.userAgent)
const isIos = /(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)
const cache = new Map()
let initialUrl

export function prepare(Vue) {
    Vue.config.ignoredElements = ['wx-open-launch-weapp', 'wx-open-launch-app']
    if (!weixin) {
        return
    }
    if (isIos) {
        initialUrl = window.location.href
    }
}

export function isWeixin() {
    return weixin
}

export async function getJsTicket({ jsApiList = [], openTagList = [] }) {
    if (!weixin) {
        return new Promise(function(_, reject) {
            reject('请在微信中打开')
        })
    }

    let url = window.location.href
    if (isIos && initialUrl) {
        url = initialUrl
    }
    url = encodeURIComponent(url)

    let config
    if (cache.has(url)) {
        config = cache.get(url)
    } else {
        config = await getJsonResult('/wx/js-ticket?url=' + url)
    }
    cache.set(url, config)

    // config.debug = true
    config.jsApiList = jsApiList
    config.openTagList = openTagList
    wx.config(config)

    return new Promise(function(resolve, reject) {
        wx.ready(function() {
            console.log('weixin loaded for', url)
            resolve()
        })
        wx.error(function({ errMsg }) {
            console.log('网站微信认证失败', errMsg)
            // alert('网站微信认证失败' + errMsg)
            reject(errMsg)
        })
    })
}

export function scanQRCode(option) {
    option = Object.assign(
        {
            needResult: 1
        },
        option
    )
    return new Promise(function(resolve, reject) {
        wx.scanQRCode({
            needResult: option.needResult,
            scanType: ['qrCode', 'barCode'],
            success: function({ resultStr, errMsg }) {
                console.log('qrcode', resultStr, errMsg)
                if ('scanQRCode:ok' === errMsg) {
                    resolve(resultStr)
                } else {
                    reject(errMsg)
                }
            }
        })
    })
}
